import { Component, OnInit } from '@angular/core';
import { ValidateService } from './../../services/validate.service';
import { AuthService } from './../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;

  constructor(
    private validateService: ValidateService,
    private authService: AuthService,
    private router: Router,
    private flashMessages: FlashMessagesService
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const newUser = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }

    // Required Fields
    if (!this.validateService.validateRegister(newUser)) {
      this.flashMessages.show('Please filled all fields', {
        cssClass: 'alert-danger',
        timeout: 3000
      });
      return false;
    }

    // Validate Email
    if (!this.validateService.validateEmail(newUser.email)) {
      this.flashMessages.show('Please use a valid email', {
        cssClass: 'alert-danger',
        timeout: 3000
      });
      return false;
    }

    // Register User
    this.authService.registerUser(newUser)
      .subscribe(data => {
        if (data.success) {
          this.flashMessages.show('You are now registered and can log in', {
            cssClass: 'alert-success',
            timeout: 3000
          });
          this.router.navigate(['/login']);
        } else {
          this.flashMessages.show('Something went wrong', {
            cssClass: 'alert-danger',
            timeout: 3000
          });
          this.router.navigate(['/register']);
        }
      });

  }

}
